.cancel-lightbox {
    position: fixed;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    width: 75%;
    max-width: 500px; 
    height: -webkit-fit-content; 
    height: -moz-fit-content; 
    height: fit-content;
    top: 20vh;
    padding: 1em 2em;
    text-align: center;
    background-color: #FFF;
    border-radius: 20px;
    left: 50%;
    margin-left: -37.5%;
    z-index: 20000;
  
    -webkit-animation-name: pop-lightbox;
  
            animation-name: pop-lightbox;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}
@-webkit-keyframes pop-lightbox {
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}
@keyframes pop-lightbox {
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}

.cancel-container {
    -ms-flex-item-align: center;
        align-self: center;
    margin: auto;
}

.cancel-lightbox .button {
    padding: 0.5em 0.75em;
    margin: 0.5em;
}
.confirm {
    color: #FFF;
    font-weight: 600;
    background: -webkit-gradient(linear,left top, left bottom,from(#ff0000),to(#ff2d2d));
    background: -webkit-linear-gradient(#ff0000,#ff2d2d);
    background: -o-linear-gradient(#ff0000,#ff2d2d);
    background: linear-gradient(#ff0000,#ff2d2d);
    border: 1px solid #ff0000;

    -webkit-box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25);
} .confirm:active {
    background: -webkit-gradient(linear,left top, left bottom,from(#d60000),to(#e42929));
    background: -webkit-linear-gradient(#d60000,#e42929);
    background: -o-linear-gradient(#d60000,#e42929);
    background: linear-gradient(#d60000,#e42929);
}

@media (min-width: 600px){
    .cancel-lightbox {
        width: 500px;
        left: 50%;
        top: 35vh;
        margin-left: -250px;
        -webkit-animation-name: pop-lightbox;
                animation-name: pop-lightbox;
    }
    .textbox {
        margin-bottom: unset;
    }
    .button {
        padding: 0.5em;
        margin: 2em 1em;
    }
    @-webkit-keyframes pop-lightbox {
        from{
            opacity: 0;
        }
        to{
            opacity: 1;
        }
    }
    @keyframes pop-lightbox {
        from{
            opacity: 0;
        }
        to{
            opacity: 1;
        }
    }
}


